                    <img class="ad-img auto-img" src="https://file.40017.cn/appresource/image/pc/redpackCenter/pc_default_banner02.jpg" border="0"  alt="">
                </a>
                <div class="ad-text">广告</div>
            </div>
        </div>
        <!-- <div class="banner-content clearfix">
            <div class="banner-box fl">
                <div class="block">
                    <el-carousel height="400px">
                    <el-carousel-item v-for="(item,index) in TraveleData.slice(0,6)" :key="index" >
                        <img class="imgs auto-img" :src="item.images" alt="">
                    </el-carousel-item>
                    </el-carousel>
                </div>
            </div>
            <div class="cart-box fl ">
                <div class="cart-item" v-for="(item,index) in TraveleData.slice(8,10)" :key="index" >
                    <img class="imgs" :src="item.images" alt="">
                    <div class="text">{{item.title}}</div>
                </div>
            </div>
        </div> -->
        <div class="newList-content">
            <div class="clearfix">
                <div class="new-list-left-box fl">
                     <div class="list-item fl" v-for="(item,index) in TraveleData" :key="index" @click="goDetail(item.id)">
                        <div class="left-img">
                            <el-image class=" err-imgs">
                                <div slot="error" class="image-slot">
                                    <i class="el-icon-picture-outline"></i>
                                </div>
                            </el-image>
                            <img class="a-img auto-img" :src="item.images" alt="">
                        </div>
                        <div class="right-cont ">
                            <div class="title">{{item.title}}</div>
                            <div class="text-cont">
                                <span class="post-name">{{item.posterNickName}}</span>
                                <span class="pub-time">{{item.publishTime}}</span>
                            </div>
                        </div>
                    </div>

                     <div class="pagination-page fl">
                        <el-pagination 
                        class="pag-box" 
                        background 
                        layout="prev, pager, next" 
                        :size-change="10"
                        :total="1000"
                        :current-change = "currentPage"
                        @current-change="handleCurrentChange"
                        >
                        </el-pagination>
                    </div>
                   
                </div>

                <div class="new-other-right-box fl">
                    <div class="other-list">
                        <div class="other-title">
                                <div class="ot-name">(九月)旅游推荐</div>
                        </div>
                        <div class="re-item">
                            <ul>
                                <li class="item" v-for="item in recomTravelData" :key="item">{{item}}</li>
                            </ul>
                        </div>
                        <div class="other-box-one">

                                <div class="car-item">
                                    <a href="#" target="_blank" >
                                        <img class="auto-img" src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2810682360,1427186553&fm=26&gp=0.jpg" alt="">
                                    </a>
                                    <div class="text">广告</div>
                                </div>
                        </div>
                        <div class="other-box-one">
                                <div class="car-item">
                                    <a href="#" target="_blank" >
                                        <img class="auto-img" src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2187425743,3903390512&fm=26&gp=0.jpg" alt="">
                                    </a>
                                    <div class="text">广告</div>
                                </div>
                        </div>
                       
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import "../../assets/css/travel.less";
    export default {
        name:'travel',
        data(){
            return{
                currentPage:1,
                // 定义页数
                pageNum:'',
                TraveleData:[],
                recomTravelData:['苍山洱海','稻城亚丁','香格里拉','玉龙雪山','大理古城','桂林漓江','武功山','呼伦贝尔大草原','厦门鼓浪屿','张家界','九寨沟','杭州千岛湖','凤凰古城','黄桃古镇','婺源','故宫博物院','都匀影视城','涠洲岛'],
            }
        },
        created(){
            this.getTraveleData()
        },
        methods:{

            getTraveleData(pageNum){
                this.axios({
                    method:'GET',
                    url:'/list',
                    params:{
                        apikey: this.appkey,
                        category:'T1348654204705',
                        page:pageNum,
                    }
                }).then((result) =>{
                    // console.log('result',result)
                    this.TraveleData = result.data.data
                }).catch((err) =>{
                    // console.log('err',err)
                })
            },
             //点击页码，获取对应的val值，在传给要显示的页数内容
            handleCurrentChange(val){
                
                // console.log('val',val)
                let pageNum = val;
                // console.log('pageNum',pageNum)
                //调用请求数据，才能获取值
                this.getTraveleData(pageNum)
            
            },
            goDetail(id) {
                // console.log(id);
                this.$router.push({ name: "Detail", query: { id } });
            },
        }
    }
</script>

